import React, { useState, useEffect } from 'react';
// import { useNavigate } // Optionally, if you want to redirect unauthenticated users
import useFetchData from './hooks/useFetchData'; // This will now use the Axios version
import DataCard from './shared/DataCard';
import DataTable from './shared/DataTable';
import LoadingSpinner from './shared/LoadingSpinner';
import ErrorMessage from './shared/ErrorMessage';
import InputForm from './shared/InputForm';
import Sidebar from './Sidebar'; // Assuming Sidebar is a component in the same directory
import './InvestmentTools.scss' // Specific styles for this page

const baseUrl = process.env.REACT_APP_API_BASE_URL;

// Helper formatting functions (can be moved to a utils file)
const formatToLocaleString = (number) => {
  if (typeof number !== 'number') return number; // or handle as error/empty string
  return Number(number).toLocaleString(); // e.g., 1000 -> "1,000"
};

const formatNumberToFixed = (number, digits = 4) => {
    if (typeof number !== 'number') return number;
    return Number(number).toFixed(digits);
};


if (!baseUrl) {
  console.error(
    "CRITICAL ERROR: REACT_APP_API_BASE_URL is not defined in your .env file. " +
    "API calls will fail. Please set it (e.g., http://localhost:3000/api/market " +
    "or your actual API URL) and restart the development server."
  );
} else {
  console.log("[InvestmentTools] Using API Base URL:", baseUrl);
}

const apiOptions = [
  { value: 'globalQuote', label: 'Global Quote' },
  { value: 'intraday', label: 'Intraday Data' },
  { value: 'daily', label: 'Daily Data' },
  { value: 'dailyAdjusted', label: 'Daily Adjusted Data' },
  { value: 'weekly', label: 'Weekly Data' },
  { value: 'weeklyAdjusted', label: 'Weekly Adjusted Data' },
  { value: 'monthly', label: 'Monthly Data' },
  { value: 'monthlyAdjusted', label: 'Monthly Adjusted Data' },
];

const InvestmentTools = () => {
  const [authToken, setAuthToken] = useState(null);
  const [currentUser, setCurrentUser] = useState(null);
  const [currentUserRole, setCurrentUserRole] = useState(null);
  const [authChecked, setAuthChecked] = useState(false);

  const [activeApi, setActiveApi] = useState(apiOptions[0].value);
  const [symbol, setSymbol] = useState('AAPL'); // Default symbol
  const [intradayInterval, setIntradayInterval] = useState('5min'); // Default for form
  const [timeSeriesOutputSize, setTimeSeriesOutputSize] = useState('compact'); // Default for form

  // const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem("authToken");
    const role = localStorage.getItem("userRole");
    const storedUserDataString = localStorage.getItem("userData");

    if (token) {
      setAuthToken(token);
      setCurrentUserRole(role);
      if (storedUserDataString) {
        try {
          setCurrentUser(JSON.parse(storedUserDataString));
        } catch (e) {
          console.error("[InvestmentTools] Failed to parse user data from localStorage:", e);
          setCurrentUser(null);
        }
      }
    } else {
      console.warn("[InvestmentTools] No authentication token found. User may not be able to access all features.");
    }
    setAuthChecked(true);
  }, []);

  const { data, loading, error, makeRequest, clear } = useFetchData(authToken);

  const handleApiChange = (newApiType) => {
    setActiveApi(newApiType);
    clear(); 
    if (newApiType === 'monthly' || newApiType === 'monthlyAdjusted') {
        setSymbol('IBM'); 
    } else {
        setSymbol('AAPL'); 
    }
    setIntradayInterval('5min');
    setTimeSeriesOutputSize('compact');
  };

  const handleSubmit = (formData) => {
    if (!authToken) { 
      console.error("[InvestmentTools] Attempted to fetch data without authentication.");
      return;
    }
    if (!baseUrl) {
      console.error("[InvestmentTools] API Base URL is not configured. Cannot make request.");
      return;
    }

    const currentSymbol = formData.symbol?.trim().toUpperCase() || symbol;
    setSymbol(currentSymbol); 

    let url = '';
    let pathSegment = ''; 

    switch (activeApi) {
      case 'globalQuote':
        pathSegment = `/quote/${currentSymbol}`;
        break;
      case 'intraday':
        const interval = formData.interval?.trim() || intradayInterval;
        setIntradayInterval(interval); 
        pathSegment = `/intraday/${currentSymbol}/${interval}`;
        break;
      case 'daily':
        const outputSizeDaily = formData.outputsize?.trim() || timeSeriesOutputSize;
        setTimeSeriesOutputSize(outputSizeDaily);
        pathSegment = `/daily/${currentSymbol}/${outputSizeDaily}`;
        break;
      case 'dailyAdjusted':
        const outputSizeDailyAdj = formData.outputsize?.trim() || timeSeriesOutputSize;
        setTimeSeriesOutputSize(outputSizeDailyAdj);
        pathSegment = `/daily-adjusted/${currentSymbol}/${outputSizeDailyAdj}`;
        break;
      case 'weekly':
        pathSegment = `/weekly/${currentSymbol}`;
        break;
      case 'weeklyAdjusted':
        pathSegment = `/weekly-adjusted/${currentSymbol}`;
        break;
      case 'monthly':
        pathSegment = `/monthly/${currentSymbol}`;
        break;
      case 'monthlyAdjusted':
        pathSegment = `/monthly-adjusted/${currentSymbol}`; 
        break;
      default:
        console.error('[InvestmentTools] Unknown API type selected:', activeApi);
        return;
    }

    if (pathSegment) {
      url = `${baseUrl}${pathSegment}`;
      console.log('[InvestmentTools] Making API request to:', url);
      makeRequest(url); // Axios config can be passed as a second argument if needed
    } else {
      console.error('[InvestmentTools] Could not construct API path for:', activeApi);
    }
  };

  const getFormFields = () => {
    const commonSymbolField = {
      name: 'symbol',
      label: 'Stock Symbol',
      defaultValue: symbol,
      placeholder: 'e.g., AAPL or IBM'
    };

    switch (activeApi) {
      case 'globalQuote':
        return [commonSymbolField];
      case 'intraday':
        return [
          commonSymbolField,
          {
            name: 'interval',
            label: 'Interval',
            defaultValue: intradayInterval,
            placeholder: '1min, 5min, 15min, 30min, 60min',
          }
        ];
      case 'daily':
      case 'dailyAdjusted':
        return [
          commonSymbolField,
          {
            name: 'outputsize',
            label: 'Output Size',
            defaultValue: timeSeriesOutputSize,
            placeholder: 'compact or full',
          }
        ];
      case 'weekly':
      case 'weeklyAdjusted':
      case 'monthly':
      case 'monthlyAdjusted':
        return [commonSymbolField];
      default:
        return [];
    }
  };

  const getTableColumns = () => {
    switch (activeApi) {
      case 'intraday':
        return [
            { Header: 'Timestamp', accessor: 'timestamp' },
            { Header: 'Open', accessor: 'open', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'High', accessor: 'high', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Low', accessor: 'low', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Close', accessor: 'close', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Volume', accessor: 'volume', Cell: ({value}) => formatToLocaleString(value) },
        ];
      case 'daily':
      case 'weekly':
      case 'monthly':
        return [
            { Header: 'Date', accessor: 'date' },
            { Header: 'Open', accessor: 'open', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'High', accessor: 'high', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Low', accessor: 'low', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Close', accessor: 'close', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Volume', accessor: 'volume', Cell: ({value}) => formatToLocaleString(value) },
        ];
      case 'dailyAdjusted':
        return [
            { Header: 'Date', accessor: 'date' },
            { Header: 'Open', accessor: 'open', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'High', accessor: 'high', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Low', accessor: 'low', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Close', accessor: 'close', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Adjusted Close', accessor: 'adjustedClose', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Volume', accessor: 'volume', Cell: ({value}) => formatToLocaleString(value) },
            { Header: 'Dividend Amt', accessor: 'dividendAmount', Cell: ({value}) => formatNumberToFixed(value, 4) },
            { Header: 'Split Coeff.', accessor: 'splitCoefficient', Cell: ({value}) => formatNumberToFixed(value, 1) },
        ];
      case 'weeklyAdjusted':
      case 'monthlyAdjusted': 
        return [
            { Header: 'Date', accessor: 'date' },
            { Header: 'Open', accessor: 'open', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'High', accessor: 'high', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Low', accessor: 'low', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Close', accessor: 'close', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Adjusted Close', accessor: 'adjustedClose', Cell: ({value}) => formatNumberToFixed(value) },
            { Header: 'Volume', accessor: 'volume', Cell: ({value}) => formatToLocaleString(value) },
            { Header: 'Dividend Amt', accessor: 'dividendAmount', Cell: ({value}) => formatNumberToFixed(value, 4) },
        ];
      default:
        return []; 
    }
  };

  const getTableTitle = () => {
    const currentApiOption = apiOptions.find(opt => opt.value === activeApi);
    let title = currentApiOption ? currentApiOption.label : 'Data';
    const activeSymbol = (activeApi === 'globalQuote' && data?.symbol)
                       ? data.symbol
                       : (data?.metaData?.['2. Symbol'] || symbol); 
    if (activeSymbol) {
      title += ` for ${activeSymbol}`;
    }
    if (data?.metaData?.['4. Interval'] && activeApi === 'intraday') {
      title += ` (${data.metaData['4. Interval']})`;
    }
    return title;
  };

  const renderData = () => {
    if (loading) return <LoadingSpinner />;
    if (error) return <ErrorMessage message={error} />;
    if (!data) return <p className="no-data-message">Select an API and enter parameters to fetch data.</p>;

    if (activeApi === 'globalQuote') {
      const displayData = {
        "Symbol": data.symbol,
        "Open": formatNumberToFixed(data.open),
        "High": formatNumberToFixed(data.high),
        "Low": formatNumberToFixed(data.low),
        "Price": formatNumberToFixed(data.price),
        "Volume": formatToLocaleString(data.volume),
        "Latest Trading Day": data.latestTradingDay,
        "Previous Close": formatNumberToFixed(data.previousClose),
        "Change": formatNumberToFixed(data.change, 2), // Assuming change is a number
        "Change Percent": data.changePercent 
      };
      return <DataCard title={getTableTitle()} data={displayData} />;
    }

    return (
      <>
        {data.metaData && <DataCard title="Metadata" data={data.metaData} />}
        {data.data && Array.isArray(data.data) && data.data.length > 0 ? (
          <DataTable
            columns={getTableColumns()}
            data={data.data} 
            itemsPerPage={15}
            title={getTableTitle()}
          />
        ) : (
          <p className="no-data-message">No time series data found for the specified parameters, or data is not in the expected array format.</p>
        )}
      </>
    );
  };

  if (!authChecked) {
    return <LoadingSpinner />; 
  }

  if (!authToken) { 
    return (
      <div className="dashboard-page investment-tools-page">
        <Sidebar /> 
        <div className="auth-message-container" style={{ padding: '20px', textAlign: 'center' }}>
          <h2>Access Denied</h2>
          <ErrorMessage message="You need to be logged in to use the Investment Tools. Please log in and try again." />
        </div>
      </div>
    );
  }

  return (
    <div className="dashboard-page investment-tools-page">
      <div className='main-content-investment-tool'> 
        <Sidebar /> 
        <div className="tool-container"> 
            <h2>Investment Tools - Alpha Vantage API</h2>
            {currentUser && (
            <p className="user-greeting">
                Welcome, {currentUser.username || currentUser.name || 'User'}!
                {currentUserRole && ` (Role: ${currentUserRole})`}
            </p>
            )}
            <div className="api-selector">
            <label htmlFor="api-select">Select API: </label>
            <select id="api-select" value={activeApi} onChange={(e) => handleApiChange(e.target.value)}>
                {apiOptions.map(option => (
                <option key={option.value} value={option.value}>{option.label}</option>
                ))}
            </select>
            </div>
            <InputForm
            key={activeApi} 
            fields={getFormFields()}
            onSubmit={handleSubmit}
            isLoading={loading}
            buttonText={`Fetch ${apiOptions.find(opt => opt.value === activeApi)?.label || 'Data'}`}
            />
            <div className="data-display-area">
            {renderData()}
            </div>
        </div>
      </div>
    </div>
  );
};

export default InvestmentTools;