// SidebarAdmin.jsx
import React from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import logo3 from "../../assets/image/logo3.png";
import "../../assets/styles/AdminSidebar.scss"; // Styles for this specific sidebar

// Destructure isOpen from props
const SidebarAdmin = ({ onLogout, isOpen }) => {
  const navigate = useNavigate();

  const handleSignOut = () => {
    if (typeof onLogout === 'function') {
      onLogout();
    } else {
      localStorage.removeItem('authToken');
      localStorage.removeItem('userRole');
      localStorage.removeItem('userData');
      navigate('/login');
    }
  };

  return (
    // Use a more specific class for the sidebar itself, e.g., "admin-sidebar"
    // Conditionally add "open" class based on isOpen prop
    // Removed the outer <div className="dashboard">
    <aside className={`admin-sidebar ${isOpen ? "open" : ""}`}>
      <div className="admin-sidebar__logo-section"> {/* Changed class */}
        <img src={logo3} alt="logo" height="50px" width="50px" />
        <h2 className="admin-sidebar__title">Admin Dashboard</h2> {/* Changed class */}
      </div>
      <ul className="admin-sidebar__menu"> {/* Changed class */}
        <li className="admin-sidebar__menu-item"> {/* Changed class */}
          <NavLink 
            to="/adminDashboard" 
            end 
            className={({ isActive }) => isActive ? "active-link" : ""} // Simpler active class
          >
            Invest Dashboard
          </NavLink>
        </li>
        <li className="admin-sidebar__menu-item">
          <NavLink 
            to="/adminDashboard/userManagement" 
            className={({ isActive }) => isActive ? "active-link" : ""}
          >
            User Management
          </NavLink>
        </li>
        {/* ... other NavLinks with updated classes ... */}
        <li className="admin-sidebar__menu-item">
            <NavLink to="/adminDashboard/manageBlog" className={({ isActive }) => isActive ? "active-link" : ""}>
              Manage Blog
            </NavLink>
          </li>
          <li className="admin-sidebar__menu-item">
            <NavLink to="/adminDashboard/transaction" className={({ isActive }) => isActive ? "active-link" : ""}>
              Transactions
            </NavLink>
          </li>
          <li className="admin-sidebar__menu-item">
            <NavLink to="/adminDashboard/withdrawal" className={({ isActive }) => isActive ? "active-link" : ""}>
              Withdrawals
            </NavLink>
          </li>
      </ul>
      <div 
        className="admin-sidebar__signout" /* Changed class */
        onClick={handleSignOut} 
        style={{ cursor: 'pointer' }}
        tabIndex={0} // Make it focusable
        role="button" // ARIA role
      >
        Sign Out
      </div>
    </aside>
  );
};

export default SidebarAdmin;