// Login.jsx
import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import Header from "../Component/Header";
import Footer from "../Component/Footer";
import Seo from "../Component/Seo";
import logo1 from "../assets/image/logo1.png";
import logo6 from "../assets/image/logo6.png";
import loginAnimation from "../assets/image/Login_no_bg_v2.gif";
import "../assets/styles/Login.scss";

const baseUrl = process.env.REACT_APP_API_BASE_URL;

// InputField component remains the same
const InputField = ({ label, type, id, registerProps, error, showToggle, showPassword, togglePassword }) => (
  <div className="form-group">
    <label htmlFor={id}>{label}</label>
    <div style={{ position: 'relative' }}> {/* Added for password toggle positioning if needed */}
      <input
        type={showToggle && !showPassword ? "password" : type}
        id={id}
        {...registerProps}
        placeholder={`Enter your ${label.toLowerCase()}`}
      />
      {showToggle && (
        <button
          type="button"
          onClick={togglePassword}
          style={{ position: 'absolute', right: '10px', top: '50%', transform: 'translateY(-50%)', background: 'none', border: 'none', cursor: 'pointer' }}
          aria-label={showPassword ? "Hide password" : "Show password"}
        >
          {showPassword ? "Hide" : "Show"}
        </button>
      )}
    </div>
    {error && <p className="error">{error.message}</p>}
  </div>
);


const LoginRegister = ({ onLoginSuccess }) => {
  const [isRegister, setIsRegister] = useState(false);
  const [showOtpField, setShowOtpField] = useState(false);
  const [enteredOtp, setEnteredOtp] = useState(Array(6).fill(""));
  const [phoneNumber, setPhoneNumber] = useState("");
  const [isOtpVerified, setIsOtpVerified] = useState(false);
  const [registrationData, setRegistrationData] = useState({});
  const [showPassword, setShowPassword] = useState(false); // For registration
  const [showLoginPassword, setShowLoginPassword] = useState(false); // For login
  const [loading, setLoading] = useState(false);
  const [otpCooldown, setOtpCooldown] = useState(0);
  const navigate = useNavigate();
  const baseUrl = process.env.REACT_APP_API_BASE_URL;

  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    reset,
  } = useForm();

  const toggleForm = () => {
    setIsRegister(!isRegister);
    setShowOtpField(false);
    setIsOtpVerified(false);
    setEnteredOtp(Array(6).fill(""));
    reset();
  };

  useEffect(() => {
    if (showOtpField) {
      document.getElementById("otp-input-0")?.focus();
    }
  }, [showOtpField]);

  useEffect(() => {
    let timer;
    if (otpCooldown > 0) {
      timer = setTimeout(() => setOtpCooldown(otpCooldown - 1), 1000);
    }
    return () => clearTimeout(timer);
  }, [otpCooldown]);

  const handleRegistration = async (data) => {
    setLoading(true);
    try {
      await axios.post(`${baseUrl}/api/users/verify-phone`, { phone: data.phone });
      toast.success("OTP Sent! Check your phone.");
      setPhoneNumber(data.phone);
      setRegistrationData(data);
      setShowOtpField(true);
      setOtpCooldown(60);
    } catch (error) {
      toast.error(error.response?.data?.message || "Error sending OTP");
    } finally {
      setLoading(false);
    }
  };

  const resendOtp = async () => {
    if (otpCooldown > 0) return;
    setLoading(true);
    try {
      await axios.post(`${baseUrl}/api/users/verify-phone`, { phone: phoneNumber });
      toast.success("OTP Resent!");
      setOtpCooldown(60);
    } catch (error) {
      toast.error(error.response?.data?.message || "Error resending OTP");
    } finally {
      setLoading(false);
    }
  };

  const verifyOtp = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      await axios.post(`${baseUrl}/api/users/verify-phone/verify`, {
        phone: phoneNumber,
        otp: enteredOtp.join(""),
      });
      toast.success("OTP Verified! Please complete your registration.");
      setIsOtpVerified(true);
    } catch (error) {
      toast.error(error.response?.data?.message || "Invalid OTP or Verification Failed.");
    } finally {
      setLoading(false);
    }
  };

  const registerUser = async () => {
    setLoading(true);
    try {
      // Include all necessary fields for registration from registrationData
      const finalRegistrationData = {
        name: registrationData.name,
        email: registrationData.email,
        phone: registrationData.phone, // This is phoneNumber, ensure it's correct
        password: registrationData.password,
        // Add any other fields your backend /api/users/register endpoint expects
      };
      await axios.post(`${baseUrl}/api/users/register`, finalRegistrationData);
      toast.success("Registration Successful! Please log in.");
      setIsRegister(false);
      setShowOtpField(false);
      setIsOtpVerified(false);
      setEnteredOtp(Array(6).fill(""));
      reset();
    } catch (error) {
      toast.error(error.response?.data?.message || "Registration failed");
    } finally {
      setLoading(false);
    }
  };

  // Helper function to decode JWT tokens
  const parseJwt = (token) => {
    try {
      const base64Url = token.split('.')[1];
      const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
      const jsonPayload = decodeURIComponent(
        atob(base64)
          .split('')
          .map((c) => '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2))
          .join('')
      );
      return JSON.parse(jsonPayload);
    } catch (error) {
      console.error("Error parsing JWT:", error);
      return {};
    }
  };

  const onLogin = async (data) => {
    setLoading(true);
    try {
      // Step 1: Login and get the token
      const loginRes = await axios.post(
        `${baseUrl}/api/users/login`,
        {
          email: data.email,
          password: data.password,
        }
      );

      console.log("Login.jsx: API Login Response Data:", loginRes.data);

      // Get token from response
      const token = loginRes.data.token;

      if (token) {
        // Decode the JWT to get the role
        const tokenPayload = parseJwt(token);
        console.log("Login.jsx: Decoded token payload:", tokenPayload);

        // Get role from token payload
        const userRoleFromToken = tokenPayload.role;
        console.log("Login.jsx: Role from token:", userRoleFromToken);

        if (userRoleFromToken) {
          toast.success("Login Successful!");

          // Build user object with data we have from the token
          const user = {
            id: tokenPayload.userId,
            role: userRoleFromToken,
            // add other info if available in token
          };

          if (typeof onLoginSuccess === "function") {
            onLoginSuccess(token, userRoleFromToken, user);
          } else {
            console.warn(
              "Login.jsx: onLoginSuccess prop is not a function. Storing auth data in localStorage as fallback."
            );
            localStorage.setItem("authToken", token);
            localStorage.setItem("userRole", userRoleFromToken);
            localStorage.setItem("userData", JSON.stringify(user));
          }

          // Navigate based on role
          if (userRoleFromToken === "admin") {
            navigate("/adminDashboard");
          } else {
            navigate("/dashboard"); // Regular user dashboard
          }
        } else {
          console.error("Login.jsx: Could not extract role from token");
          toast.error("Login failed: User role not found in response");
        }
      } else {
        console.error("Login.jsx: Login response missing token:", loginRes.data);
        toast.error("Login failed: No token received from server");
      }
    } catch (error) {
      if (error.response) {
        console.error("Login.jsx: Login API Error Response:", error.response);
        toast.error(
          error.response.data?.message ||
            `Login failed: ${error.response.status}`
        );
      } else if (error.request) {
        console.error("Login.jsx: Login API No Response:", error.request);
        toast.error("Login failed: No response from server. Check network.");
      } else {
        console.error("Login.jsx: Login API Request Setup Error:", error.message);
        toast.error("Login failed: An error occurred. Please try again.");
      }
    } finally {
      setLoading(false);
    }
  };

  // ... (rest of your component: OTP forms, toggleForm, JSX structure)
  // Ensure your InputField component gets the togglePassword prop correctly for login password field
  return (
    <div className="auth-container">
      <Seo title={isRegister ? "Register" : "Login"} description={isRegister ? "Create your account" : "Log in to your account"} page={isRegister ? "Register" : "Login"} keywords="wealth, financial freedom, risk management, strategies" />
      <Header />
      <div className="main-login">
        <div className="auth-card">
          <div className="logo-section1">
            <img src={logo1} alt="The Capital Tree Logo" className="logo1-img" height="50" width="50" />
            <h1 className="brand-name">TheCapitalTree</h1>
          </div>

          {isRegister ? (
            <>
              <h2 className="auth-title">Register</h2>
              <p className="auth-subtitle">Create an account to get started</p>

              {!showOtpField ? (
                <form className="auth-form" onSubmit={handleSubmit(handleRegistration)}>
                  <InputField label="Name" type="text" id="registerName" registerProps={register("name", { required: "Name is required" })} error={errors.name} />
                  <InputField label="Email" type="email" id="registerEmail" registerProps={register("email", { required: "Email is required", pattern: { value: /^\S+@\S+$/i, message: "Invalid email address" } })} error={errors.email} />
                  <InputField label="Phone" type="tel" id="registerPhone" registerProps={register("phone", {
                    required: "Phone number is required",
                    pattern: { value: /^\+?[0-9]{10,15}$/, message: "Enter a valid phone number" },
                  })} error={errors.phone} />
                  <InputField
                    label="Password"
                    type="password" // Type is managed internally by InputField based on showPassword
                    id="registerPassword"
                    showToggle
                    showPassword={showPassword}
                    togglePassword={() => setShowPassword(!showPassword)}
                    registerProps={register("password", {
                      required: "Password is required",
                      pattern: {
                        value: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\W).{8,16}$/,
                        message: "Min 8 chars: 1 uppercase, 1 lowercase, 1 special char.",
                      },
                    })}
                    error={errors.password}
                  />
                  <InputField
                    label="Confirm Password"
                    type="password" // Type is managed internally
                    id="confirmRegisterPassword"
                    showToggle
                    showPassword={showPassword} // Use the same state for both password fields in register
                    togglePassword={() => setShowPassword(!showPassword)}
                    registerProps={register("confirmPassword", {
                      required: "Confirm password is required",
                      validate: (value) => value === watch("password") || "Passwords do not match",
                    })}
                    error={errors.confirmPassword}
                  />
                  <button type="submit" className="auth-button register-button" disabled={loading}>
                    {loading ? "Sending OTP..." : "Register"}
                  </button>
                </form>
              ) : (
                <>
                  <form className="otp-form" onSubmit={verifyOtp}>
                    <div className="form-group otp-boxes">
                      <label>Verify OTP sent to {phoneNumber}</label>
                      <div className="otp-inputs">
                        {enteredOtp.map((digit, index) => (
                          <input
                            key={index}
                            type="text"
                            maxLength="1"
                            className="otp-input"
                            id={`otp-input-${index}`}
                            value={digit}
                            onChange={(e) => {
                              const value = e.target.value.replace(/[^0-9]/g, "");
                              const newOtp = [...enteredOtp];
                              newOtp[index] = value;
                              setEnteredOtp(newOtp);
                              if (value && index < 5) {
                                document.getElementById(`otp-input-${index + 1}`)?.focus();
                              }
                            }}
                            onKeyDown={(e) => {
                              if (e.key === "Backspace" && !enteredOtp[index] && index > 0) {
                                document.getElementById(`otp-input-${index - 1}`)?.focus();
                              }
                            }}
                          />
                        ))}
                      </div>
                    </div>
                    <button type="submit" className="auth-button otp-verify-button" disabled={loading || enteredOtp.join("").length !== 6}>
                      {loading ? "Verifying..." : "Verify OTP"}
                    </button>
                  </form>
                  <button onClick={resendOtp} disabled={otpCooldown > 0 || loading} className="resend-otp-button">
                    {otpCooldown > 0 ? `Resend OTP in ${otpCooldown}s` : "Resend OTP"}
                  </button>
                  {isOtpVerified && (
                    <button className="auth-button final-register-button" onClick={registerUser} disabled={loading}>
                      {loading ? "Registering..." : "Create Account"}
                    </button>
                  )}
                </>
              )}
              <p className="toggle-message">
                Already have an account? <button type="button" onClick={toggleForm}>Log in</button>
              </p>
            </>
          ) : (
            <>
              <h2 className="auth-title-log">Log In</h2>
              <p className="auth-subtitle-log">Join for exclusive access</p>
              <form className="auth-form-log" onSubmit={handleSubmit(onLogin)}>
                <InputField
                  label="Email"
                  type="email"
                  id="loginEmail"
                  registerProps={register("email", { required: "Email is required", pattern: { value: /^\S+@\S+$/i, message: "Invalid email address" }})}
                  error={errors.email}
                />
                <InputField
                  label="Password"
                  type="password" // Type managed internally
                  id="loginPassword"
                  showToggle
                  showPassword={showLoginPassword}
                  togglePassword={() => setShowLoginPassword(!showLoginPassword)}
                  registerProps={register("password", {
                    required: "Password is required",
                  })}
                  error={errors.password}
                />
                <button type="submit" className="auth-button login-button" disabled={loading}>
                  {loading ? "Logging in..." : "Login"}
                </button>
              </form>
              <p className="toggle-message-log">
                Don’t have an account? <button type="button" onClick={toggleForm}>Register</button>
              </p>
            </>
          )}
        </div>

        <div className="loginAnimation">
          <img src={loginAnimation} alt="MoneyTree" height="300" width="360" />
          <h2>
            Welcome To <img src={logo6} alt="" height="35" className="welcomelogo" /> TheCapitalTree
          </h2>
        </div>
      </div>
      <Footer />
      <ToastContainer position="top-right" autoClose={3000} hideProgressBar={false} newestOnTop={false} closeOnClick rtl={false} pauseOnFocusLoss draggable pauseOnHover />
    </div>
  );
};

export default LoginRegister;