import React, { useEffect, useState, useCallback } from "react";
import { useNavigate } from "react-router-dom";
import "../assets/styles/Dashboard.scss"; // Make sure this path is correct
import Sidebar from "../Component/dashboard/Sidebar"; // Make sure this path is correct
import axios from "axios";

import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from "@mui/material"; // Added Table components

import { Bar } from "react-chartjs-2";
import {
  Chart as ChartJS,
  BarElement,
  CategoryScale,
  LinearScale,
  Tooltip,
  Legend,
  Title, // Added Title for chart
} from "chart.js";

ChartJS.register(
  BarElement,
  CategoryScale,
  LinearScale,
  Tooltip,
  Legend,
  Title // Register Title
);

const Dashboard = ({ onLogout }) => {
  const navigate = useNavigate();
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [userData, setUserData] = useState({});
  const [rawInvestmentSummaries, setRawInvestmentSummaries] = useState([]); // New state for raw data
  const [investmentSummaryChartData, setInvestmentSummaryChartData] = useState({
    labels: [],
    datasets: [
      {
        label: "Total Investment",
        data: [],
        backgroundColor: "#4858C2",
        borderColor: "#3A45A0",
        borderWidth: 1,
      },
    ],
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const baseUrl = process.env.REACT_APP_API_BASE_URL;

  const fetchDashboardData = useCallback(async (token, userId) => {
    if (!userId) {
      setError("User ID not available. Cannot fetch data.");
      setLoading(false);
      return;
    }
    setLoading(true);
    setError(null);

    const periods = ["daily", "weekly", "monthly", "yearly"];
    const promises = periods.map((period) =>
      axios.get(`${baseUrl}/api/transactions/summary/${userId}?period=${period}`, {
        headers: { Authorization: `Bearer ${token}` },
      })
    );

    try {
      const responses = await Promise.all(promises);
      const investmentData = responses.map(response => response.data);

      console.log("Fetched Investment Summaries:", investmentData);
      setRawInvestmentSummaries(investmentData); // Store raw data

      const labels = investmentData.map(data =>
        data.period.charAt(0).toUpperCase() + data.period.slice(1)
      );
      const dataPoints = investmentData.map(data => data.totalInvestment);

      setInvestmentSummaryChartData({
        labels: labels,
        datasets: [
          {
            label: "Total Investment Growth",
            data: dataPoints,
            backgroundColor: [
              'rgba(255, 99, 132, 0.6)',
              'rgba(54, 162, 235, 0.6)',
              'rgba(255, 206, 86, 0.6)',
              'rgba(75, 192, 192, 0.6)',
              'rgba(153, 102, 255, 0.6)',
            ],
            borderColor: [
              'rgba(255, 99, 132, 1)',
              'rgba(54, 162, 235, 1)',
              'rgba(255, 206, 86, 1)',
              'rgba(75, 192, 192, 1)',
              'rgba(153, 102, 255, 1)',
            ],
            borderWidth: 1,
          },
        ],
      });

      setLoading(false);
    } catch (err) {
      console.error("API error:", err);
      if (err.response) {
        console.error("API error response data:", err.response.data);
        console.error("API error response status:", err.response.status);
        if (err.response.status === 401 || err.response.status === 403) {
          console.log("Dashboard: API auth error, logging out");
          setIsAuthenticated(false);
          if (onLogout) onLogout();
          navigate("/login");
          return;
        }
        if (err.response.status === 404) {
           setError(`Failed to fetch dashboard data. One or more API endpoints not found. Please check console for details. Base URL: ${baseUrl}, UserID: ${userId}`);
        } else {
           setError("Failed to fetch dashboard data. Status: " + err.response.status);
        }
      } else if (err.request) {
        setError("Failed to fetch dashboard data. No response from server.");
        console.error("API error request:", err.request);
      } else {
        setError("Failed to fetch dashboard data. Error: " + err.message);
      }
      setLoading(false);
    }
  }, [baseUrl, navigate, onLogout]);

  useEffect(() => {
    const authToken = localStorage.getItem("authToken");
    const userRole = localStorage.getItem("userRole");
    const storedUserDataString = localStorage.getItem("userData");
    let parsedUserData = {};

    if (!authToken) {
      setIsAuthenticated(false);
      if (onLogout) onLogout();
      navigate("/login");
      return;
    }
    setIsAuthenticated(true);

    if (userRole === "admin") {
      navigate("/adminDashboard");
      return;
    }

    if (storedUserDataString) {
      try {
        parsedUserData = JSON.parse(storedUserDataString);
        setUserData(parsedUserData);
        console.log("Dashboard: Loaded user data:", parsedUserData);
      } catch (error) {
        console.error("Error parsing user data:", error);
      }
    } else {
        console.warn("User data not found in local storage.");
        setError("User details missing. Cannot load investment data. Please try logging in again.");
        setLoading(false); // Prevent loading indefinitely
        return; // Exit early if essential user data is missing
    }

    if (parsedUserData.id) {
      fetchDashboardData(authToken, parsedUserData.id);
    } else if (authToken) {
        console.warn("User ID not found in stored user data. Cannot fetch investment summaries.");
        setError("User ID missing from local storage. Cannot load investment data. Please try logging in again.");
        setLoading(false); // Prevent loading indefinitely
    }
  }, [navigate, onLogout, fetchDashboardData]);


  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: 'Investment Growth Over Time Periods',
        font: {
          size: 18
        }
      },
      tooltip: {
        callbacks: {
          label: function(context) {
            let label = context.dataset.label || '';
            if (label) {
              label += ': ';
            }
            if (context.parsed.y !== null) {
              label += new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(context.parsed.y);
            }
            return label;
          }
        }
      }
    },
    scales: {
      y: {
        beginAtZero: true,
        title: {
          display: true,
          text: 'Total Investment Amount ($)'
        }
      },
      x: {
        title: {
          display: true,
          text: 'Period'
        }
      }
    }
  };


  if (loading) {
    return (
      <div className="dashboard-user loading">
        <Sidebar onLogout={onLogout} setIsAuthenticated={setIsAuthenticated} />
        <div className="dashboard__main">
          <div className="loading-indicator">Loading dashboard data...</div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="dashboard-user error">
        <Sidebar onLogout={onLogout} setIsAuthenticated={setIsAuthenticated} />
        <div className="dashboard__main">
          <div className="error-message">
            <h2>Error</h2>
            <p>{error}</p>
            <button
              onClick={() => {
                const authToken = localStorage.getItem("authToken");
                const storedUserDataString = localStorage.getItem("userData");
                if (authToken && storedUserDataString) {
                  try {
                    const parsedUserData = JSON.parse(storedUserDataString);
                    if (parsedUserData.id) {
                       fetchDashboardData(authToken, parsedUserData.id);
                    } else {
                       setError("User ID missing. Cannot retry.");
                    }
                  } catch (e) {
                    setError("Error parsing user data for retry. Please login again.");
                  }
                } else {
                   setError("Authentication token or user data missing. Cannot retry. Please login again.");
                }
              }}
            >
              Try Again
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="dashboard-user">
      <Sidebar onLogout={onLogout} setIsAuthenticated={setIsAuthenticated} />
      <div className="dashboard__main">
        <main>
          <header className="dashboard-header">
            <h1>User Dashboard</h1>
            <p>Welcome, {userData?.name || userData?.username || "User"}</p>
          </header>

          <section className="dashboard__section charts">
            <Paper elevation={3} className="dashboard__chart-container">
              <div className="dashboard__chart-placeholder">
                {investmentSummaryChartData.labels.length > 0 ? (
                  <Bar data={investmentSummaryChartData} options={chartOptions} />
                ) : (
                  <p>No investment data available to display chart.</p>
                )}
              </div>
            </Paper>
          </section>

          {/* New Section for displaying data in normal form (Table) */}
          <section className="dashboard__section summary-details">
            <Paper elevation={3} className="dashboard__summary-table-container">
              <h2>Investment Summary Details</h2>
              {rawInvestmentSummaries.length > 0 ? (
                <TableContainer>
                  <Table stickyHeader aria-label="investment summary table">
                    <TableHead>
                      <TableRow>
                        <TableCell>Period</TableCell>
                        <TableCell align="right">Total Investment</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {rawInvestmentSummaries.map((summary) => (
                        <TableRow key={summary.period}>
                          <TableCell component="th" scope="row">
                            {summary.period.charAt(0).toUpperCase() + summary.period.slice(1)}
                          </TableCell>
                          <TableCell align="right">
                            {new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(summary.totalInvestment)}
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              ) : (
                <p>No summary data available to display.</p>
              )}
            </Paper>
          </section>

        </main>
      </div>
    </div>
  );
};

export default Dashboard;