// AdminDashboard.jsx
import React, { useEffect, useState } from "react";
import { useNavigate, Outlet, useLocation } from "react-router-dom";
import SidebarAdmin from "../Component/AdminDashboard/AdminSidebar"; // Adjust path if needed
import "../assets/styles/AdminDashboard.scss"; // Main layout styles

// Hamburger Icon (simple SVG example)
const HamburgerIcon = ({ onClick, isOpen }) => (
  <button
    className={`hamburger-menu ${isOpen ? "open" : ""}`}
    onClick={onClick}
    aria-label="Toggle navigation"
    aria-expanded={isOpen}
  >
    <span></span>
    <span></span>
    <span></span>
  </button>
);

const AdminDashboard = ({ onLogout }) => {
  const navigate = useNavigate();
  const location = useLocation();
  const [userData, setUserData] = useState({});
  const [isSidebarOpen, setIsSidebarOpen] = useState(window.innerWidth > 768); // Default to open on desktop

  useEffect(() => {
    const token = localStorage.getItem("authToken");
    const userRole = localStorage.getItem("userRole");
    const storedUserData = localStorage.getItem("userData");

    if (!token || userRole !== "admin") {
      console.error("AdminDashboard: Not authenticated as admin or missing token/role.");
      if (onLogout) onLogout();
      navigate("/login");
      return;
    }

    if (storedUserData) {
      try {
        setUserData(JSON.parse(storedUserData));
      } catch (error) {
        console.error("AdminDashboard: Error parsing user data:", error);
      }
    }

    // Handle resize for sidebar visibility
    const handleResize = () => {
      if (window.innerWidth <= 768) {
        setIsSidebarOpen(false);
      } else {
        setIsSidebarOpen(true);
      }
    };

    window.addEventListener('resize', handleResize);
    // Call on mount to set initial state correctly based on window size
    handleResize(); 

    return () => window.removeEventListener('resize', handleResize);

  }, [navigate, onLogout]);

  // Close sidebar on route change on mobile
  useEffect(() => {
    if (window.innerWidth <= 768 && isSidebarOpen) {
      setIsSidebarOpen(false);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.pathname]); // isSidebarOpen dependency removed to prevent loop if other effects change it

  const toggleSidebar = () => {
    setIsSidebarOpen(!isSidebarOpen);
  };

  return (
    <div className={`dashboard-layout ${isSidebarOpen ? "sidebar-visible" : "sidebar-hidden"}`}>
      <SidebarAdmin
        onLogout={onLogout}
        isOpen={isSidebarOpen}
        toggleSidebar={toggleSidebar}
      />

      <div className="main-content-Admin">
        <header className="Admin-header">
          <HamburgerIcon onClick={toggleSidebar} isOpen={isSidebarOpen} />
          <h1>Investment Insight - Admin Panel</h1>
          <p className="sub-header">Welcome, {userData.name || "Admin"}</p>
        </header>

        {/* Child routes (AdminOverview, UserManagement, etc.) will render here */}
        {/* Pass userData and onLogout to child routes via context */}
        <Outlet context={{ userData, onLogout }} />
      </div>
    </div>
  );
};

export default AdminDashboard;