// App.jsx
import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Route, Routes, Navigate } from "react-router-dom"; // Removed Outlet from here as it's not directly used at App level for these routes
import { HelmetProvider } from "react-helmet-async";

// Page Imports
import Homepage from "./Pages/Homepage";
import Login from "./Pages/Login";
import Dashboard from "./Pages/Dashboard"; // User Dashboard
import About from "./Pages/About";
import Support from "./Pages/Support";
import Invest from "./Pages/Invest";
import Learn from "./Pages/Learn";
import SwpCalculator from "./Pages/SwpCalculator";
import Services from "./Pages/Services";
import Blog from "./Component/learn/Blog";
import AdminDashboard from "./Pages/AdminDashboard"; // Admin Dashboard Layout/Page

// Component Imports
import Short60 from "./Component/learn/Short60";
import Book from "./Component/learn/Book";
import Account from "./Component/dashboard/Account";
import MarketGuides from "./Component/dashboard/MarketGuides";
import Profile from "./Component/dashboard/Profile";
import InvestmentTools from "./Component/dashboard/InvestmentTools";
// import Sidebar from "./Component/dashboard/Sidebar"; // User Sidebar - ensure it's used correctly if needed for user dashboard
import AdminOverview from "./Component/AdminDashboard/AdminOverview"; // **** IMPORT THE NEW COMPONENT ****
import UserManagement from "./Component/AdminDashboard/userManagement";
import ManageBlog from "./Component/AdminDashboard/manageBlog";
import TransactionPage from "./Component/AdminDashboard/transcation";
import WithdrawalPage from "./Component/AdminDashboard/withdrawal";
// import Header from "./Component/Header"; // Ensure it's used correctly if it's a global header

import "./App.css";

// Private Route Component
function PrivateRoute({ isAuthenticated, children }) {
  if (!isAuthenticated) {
    return <Navigate to="/login" replace />;
  }
  return children;
}

// Role-Based Route Component
function RoleBasedRoute({ isAuthenticated, userRole, allowedRoles, children }) {
  if (!isAuthenticated) {
    return <Navigate to="/login" replace />;
  }
  if (!allowedRoles || !allowedRoles.includes(userRole)) {
    // console.warn(`RoleBasedRoute: Access denied. User role: ${userRole}, Allowed: ${allowedRoles.join(', ')}`);
    return <Navigate to="/unauthorized" replace />;
  }
  return children;
}

function App() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [userRole, setUserRole] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const checkAuth = () => {
      const token = localStorage.getItem("authToken");
      const role = localStorage.getItem("userRole");
      
      if (token && role) {
        try {
          const tokenPayload = parseJwt(token);
          const currentTime = Date.now() / 1000;
          
          if (tokenPayload && tokenPayload.exp && tokenPayload.exp > currentTime) {
            setIsAuthenticated(true);
            setUserRole(role);
            // console.log("App.jsx: User authenticated with role:", role);
          } else {
            // console.log("App.jsx: Token expired or invalid");
            clearAuth();
          }
        } catch (error) {
          console.error("App.jsx: Error parsing token:", error);
          clearAuth();
        }
      } else {
        clearAuth();
      }
      setLoading(false);
    };
    checkAuth();
  }, []);

  const parseJwt = (token) => { /* ... your parseJwt function ... */ 
    try {
      const base64Url = token.split('.')[1];
      const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
      const jsonPayload = decodeURIComponent(atob(base64).split('').map(function(c) {
        return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
      }).join(''));
      return JSON.parse(jsonPayload);
    } catch (error) {
      // console.error("Error parsing JWT:", error);
      return null;
    }
  };

  const clearAuth = () => {
    localStorage.removeItem("authToken");
    localStorage.removeItem("userRole");
    localStorage.removeItem("userData");
    setIsAuthenticated(false);
    setUserRole(null);
  };

  const handleLogin = (token, role, userData) => {
    // console.log("App.jsx: Login successful. Role:", role);
    localStorage.setItem("authToken", token);
    localStorage.setItem("userRole", role);
    if (userData) {
      localStorage.setItem("userData", JSON.stringify(userData));
    }
    setIsAuthenticated(true);
    setUserRole(role);
  };

  const handleLogout = () => {
    // console.log("App.jsx: Logging out user");
    clearAuth();
    // Navigation to /login will occur due to PrivateRoute/RoleBasedRoute
    // or you can explicitly navigate here if needed, but usually not required
    // navigate('/login'); // If using useNavigate hook in App.js
  };

  if (loading) {
    return <div className="app-loading">Loading...</div>;
  }

  return (
    <HelmetProvider>
      <Router>
        <div className="app-container">
          <Routes>
            {/* Public Routes */}
            <Route path="/" element={<Homepage />} />
            <Route path="/login" element={
              isAuthenticated ? 
                (userRole === "admin" ? <Navigate to="/adminDashboard" replace /> : <Navigate to="/dashboard" replace />)
                : <Login onLoginSuccess={handleLogin} />
            } />
            {/* ... other public routes ... */}
            <Route path="/invest" element={<Invest />} />
            <Route path="/about" element={<About />} />
            <Route path="/support" element={<Support />} />
            <Route path="/learn" element={<Learn />} />
            <Route path="/swp-calculator" element={<SwpCalculator />} />
            <Route path="/services" element={<Services />} />
            <Route path="/blog" element={<Blog />} />
            <Route path="/short60" element={<Short60 />} />
            <Route path="/book" element={<Book />} />
            <Route path="/unauthorized" element={<div><h1>Access Denied</h1><p>You do not have permission to view this page.</p></div>} />

            {/* User Protected Routes */}
            <Route path="/dashboard" element={
              <PrivateRoute isAuthenticated={isAuthenticated}><Dashboard onLogout={handleLogout} /></PrivateRoute>
            } />
            <Route path="/account" element={
              <PrivateRoute isAuthenticated={isAuthenticated}><Account onLogout={handleLogout} /> </PrivateRoute>
            } />
            {/* ... other user routes ... */}
            <Route path="/marketguides" element={<PrivateRoute isAuthenticated={isAuthenticated}><MarketGuides /></PrivateRoute>} />
            <Route path="/profile" element={<PrivateRoute isAuthenticated={isAuthenticated}><Profile /></PrivateRoute>} />
            <Route path="/investmenttools" element={<PrivateRoute isAuthenticated={isAuthenticated}><InvestmentTools /></PrivateRoute>} />
            
            {/* Admin Protected Routes - Using AdminDashboard as a LAYOUT */}
            <Route 
              path="/adminDashboard"
              element={
                <RoleBasedRoute isAuthenticated={isAuthenticated} userRole={userRole} allowedRoles={["admin"]}>
                  <AdminDashboard onLogout={handleLogout} /> 
                </RoleBasedRoute>
              }
            >
              {/* Child routes render inside AdminDashboard's <Outlet /> */}
              <Route index element={<AdminOverview />} /> {/* **** UPDATED: Default is AdminOverview **** */}
              <Route path="userManagement" element={<UserManagement />} />
              <Route path="manageBlog" element={<ManageBlog />} />
              <Route path="withdrawal" element={<WithdrawalPage />} /> 
              <Route path="transaction" element={<TransactionPage />} />
            </Route>
            
            <Route 
              path="*" 
              element={
                isAuthenticated 
                  ? (userRole === "admin" ? <Navigate to="/adminDashboard" replace /> : <Navigate to="/dashboard" replace />)
                  : <Navigate to="/login" replace />
              } 
            />
          </Routes>
        </div>
      </Router>
    </HelmetProvider>
  );
}

export default App;