// src/Component/AdminDashboard/AdminOverview.jsx
import React from 'react';
import { useOutletContext } from 'react-router-dom'; // To access context from AdminDashboard
import './AdminOverview.scss' // Ensure this path is correct

// Define Card component here or import it if it's shared
const Card = ({ title, description, children }) => (
  <div className="card admin-overview-card"> {/* Add a specific class if needed for styling */}
    <h3>{title}</h3>
    {description && <p className="card-description">{description}</p>}
    <div className="card-content">{children}</div>
  </div>
);

const AdminOverview = () => {
  // Access userData and onLogout if needed by any of the cards
  const { userData, onLogout } = useOutletContext() || { userData: {}, onLogout: () => {} };

  return (
    <div className="admin-overview-content"> {/* Wrapper for the overview page */}
      <h2>Dashboard Overview</h2>
      <div className="cards-container">
        <Card
          title="Activated Investment"
          description="The Amount of Investment Currently Activated"
        >
          <h2>$20,500.00 USD</h2>
          <p>$800.00 since last week</p>
          <p>Profit today: $380.00 USD</p>
          <h4>Active Plan</h4>
          <p>4 +0.47% ↑</p>
        </Card>

        <Card
          title="Active Investment Plan"
          description="This investment plan is currently active"
        >
          <div className="progress-circle" style={{ textAlign: 'center', padding: '10px 0' }}>
            <span>Mercury</span>
            <div className="circle" style={{
                width: '80px', height: '80px', borderRadius: '50%', border: '4px solid lightblue',
                margin: '10px auto', display: 'flex', alignItems: 'center', justifyContent: 'center', position: 'relative'
            }}>
              <div className="fill" style={{ width: "100%", backgroundColor: 'dodgerblue', height: '100%', borderRadius: '50%', opacity: 0.7 }}></div>
              <span style={{ position: 'absolute', color: 'white', fontWeight: 'bold' }}>100%</span>
            </div>
            <p>4 Plans – 100% Active</p>
          </div>
        </Card>

        <Card
          title="Authentication Status"
          description="Current user authentication details"
        >
          <h4>User Role: {userData?.role || "admin"}</h4>
          <p>User ID: {userData?.id || "Unknown"}</p>
          <p>Session Status: Active</p>
          <button
            onClick={onLogout} // Assuming onLogout is passed from AdminDashboard via context
            style={{
              backgroundColor: "#f44336",
              color: "white",
              padding: "8px 16px",
              border: "none",
              borderRadius: "4px",
              cursor: "pointer",
              marginTop: "10px"
            }}
          >
            Log Out
          </button>
        </Card>
      </div>
    </div>
  );
};

export default AdminOverview;